@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}
html, body, #root {
    height: 100%;
}

body {
    background: #E5E6F0;
    -webkit-font-smoothing: antialiased;
}
body, input, button {
    font-family: Roboto, sans-serif;
}
#app {
    max-width: 1200px;
    width: 0 auto;
    padding: 60px 30px;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

#app main {
    margin-left: 30px;
}

@media (max-width: 1000px) {
    #app {
        flex-direction: column;
    }

    #app main {
        margin-left: 0;
        margin-top: 30px
    }

    #app aside {
        width: 100%;
    }
}

.counter {
    width: 20%;
    height: auto;
    margin-bottom: 20px;
    background: #FFFF;
    padding: 1px 10px;
    border-radius: 30px;
    box-sizing: border-box;
}

.counter span {
    font-size: 27px;
    font-weight: 700;
    text-align: center;
    color: #6931ca;
    position: relative;
    top: 3px;
}

@media (max-width: 760px) {
    .counter {
        width: 46%;
        height: auto;
        margin-bottom: 20px;
        background: #FFFF;
        padding: 1px 10px;
        border-radius: 30px;
    }
}

.close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
  
.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}
aside {
    width: 320px;
    background: #FFFF;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 30px 20px;
}

aside strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}

aside form {
    margin-top: 30px;
}

aside form .input-block + .input-block {
    margin-top: 20px;
}

aside form .input-group {
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

aside form .input-group .input-block {
    margin-top: 0;
}

aside form .input-block label {
    color: #ACACAC;
    font-size: 14px;
    font-weight: bold;
    display: block;
}

aside form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #EEE;
}

aside form button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #7040E7;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
}

aside form button[type=submit]:hover {
    background: #6931ca;
}
main {
    flex: 1 1;
}

main ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 1fr 1fr; */
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}

@media (max-width: 650px){
    main ul {
        grid-template-columns: 1fr;
    }
}
.btn-edit {
    width: 20%;
    border: 0;
    margin-top: 20px;
    background: #7040E7;
    border-radius: 2px;
    padding: 2px;
    font-size: 16px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
    position: absolute;
    top: 130px;
    left: 170px;
}

@media (max-width: 1000px) {
    .btn-edit {
        width: 20%;
        border: 0;
        margin-top: 20px;
        background: #7040E7;
        border-radius: 2px;
        padding: 2px;
        font-size: 16px;
        font-weight: bold;
        color: #FFF;
        cursor: pointer;
        -webkit-transition: background 0.5s;
        transition: background 0.5s;
        position: absolute;
        top: -15px;
        left: 237px;
    }
}
li.dev-item {
    position: relative;
    background: #FFFF;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 20px;
}

li.dev-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

li.dev-item header img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header .user-info {
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}

.user-info span {
    font-size: 13px;
    color: #999;
    margin-top: 2px;
}

li.dev-item p {
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0;
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}

li.dev-item a:hover {
    color: #5a2ea6;
}

.btn-delete {
    width: 30px;
    height: 30px;
    position: absolute;
    right: -8px;
    top: -9px;
    font-size: 25px;
    border-radius: 50%;
    box-shadow: 3px 6px 7px rgba(0,0,0, 0.3);
    border: none;
}

.btn-delete:hover {
    background: #6931ca;
    cursor: pointer;
}
